.content-navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 3000;
  .pre-nav {
    height: 30px;
    background-color: $gray-light;
    display: flex;
    align-items: center;
    .container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      a {
        color: $text-gray;
        font-size: 0.8rem;
        transition: all 0.3s;
        &:hover {
          color: $orange;
          svg {
            path {
              fill: $orange;
            }
          }
        }
        svg {
          height: 20px;
          margin-right: 0.2rem;
          path {
            fill: $text-gray;
            transition: all 0.3s;
          }
        }
      }
      .right, .left {
        display: flex;
        align-items: center;
        a {
          display: flex;
          align-items: center;
        }
      }
      .line {
        border-right: 2px solid $text-gray;
      }
    }
  }
  .navbar {
    background-color: white;
    .logo-nav {
      height: 70px;
    }
    .nav-item {
      display: flex;
      align-items: center;
      margin: 0 auto;
      .nav-link {
        @media (min-width: 1200px) {
          padding: 0 1rem;
        }
        color: $text-gray !important;
        &:hover {
          color: $orange !important;
          svg {
            path {
              fill: $orange;
            }
          }
        }
        &.line {
          @media (min-width: 1200px) {
            border-right: 1px solid $orange;
          }
        }
        svg {
          width: 30px;
          path {
            fill: $text-gray;
            transition: all 0.3s;
          }
        }
      }
    }
    .dropdown-toggle::after {
      vertical-align: middle;
      border-top: .3em solid $orange;
      border-right: .3em solid transparent;
      border-bottom: 0;
      border-left: .3em solid transparent;
    }
  }
}