.searcher {
  position: relative;
  padding-top: 2rem;
  padding-bottom: 2rem;
  .w-20 {
    width: 20%;
  }
  .dropdown-search-mobile {
    .nav {
      display: none;
    }
    .dropdown-menu.show {
      display: block !important;
    }
    .btn-dropdown-search {
      background-color: $violet;
      color: white;
      font-weight: bold;
      border: 1px solid transparent;
      box-shadow: 0px -5px 10px $text-gray;
      transition: all 0.3s;
    }
    .nav-item {
      .nav-link {
        margin: 0.5rem auto;
        width: 100%;
        background-color: white;
        border: none;
        color: $violet;
        font-weight: 800;
      }
    }
  }
  .nav-tabs {
    border: none;
    .nav-item {
      margin-right: 0.5rem;
      border: 1px solid transparent;
      .nav-link {
        background-color: $violet;
        color: white;
        font-weight: bold;
        border: 1px solid transparent;
        box-shadow: 0px -5px 10px $text-gray;
        transition: all 0.3s;
        &.active,
        &:hover {
          background-color: $orange;
        }
      }
    }
  }
  .tab-pane {
    padding: 2rem 1rem;
    background-color: white;
    box-shadow: 0px 0px 10px $text-gray;
    form {
      .border-right {
        border-right: 1px solid $violet;
      }
      label {
        color: $text-gray;
        // margin-left: 3rem;
        margin-bottom: 0;
      }
      .line-horizontal {
        display: flex;
        height: 100%;
        @media (max-width: 991.98px) {
          border-bottom: 1px solid $violet;
          padding: .7rem;
        }
        .icon {
          width: 3.5rem;
          text-align: center;
          display: flex;
          align-items: center;
          justify-content: center;
          img {
            max-height: 2.5rem;
          }
        }
        .content-select2 {
          width: calc(100% - 3.5rem);
        }
      }
      label {
        font-weight: bold;
      }
      .input-group {
        input {
          border: none;
          color: $text-gray;
          padding: 0.5rem 0;
          background-color: white;
          &:focus {
            box-shadow: none;
            outline: none;
          }
          &::placeholder {
            color: $text-gray;
          }
        }
        .icon-fa {
          color: $violet;
        }
      }
      #submit {
        font-size: 1.5rem;
      }
    }
    #btn-habs {
      cursor: pointer;
      .input-group {
        align-items: center;
        input {
          width: 20%;
          text-align: center;
        }
      }
    }
    #popover-habs,
    #popover-pasajeros,
    #popover-act,
    #popover-transf-pasajeros,
    #popover-seguro {
      position: absolute;
      background-color: white;
      padding: 1rem 0.5rem;
      min-width: 250px;
      border: 1px solid $violet;
      border-radius: 5px;
      z-index: 100;
      opacity: 0;
      transform: translateX(5000px);
      transition: opacity 0.3s;
      &.show {
        transform: translateX(0);
        opacity: 1;
      }
      [class^='content-'], [class^='content-age'] {
        display: flex;
        align-items: center;
        justify-content: space-between;
        span {
          font-size: 1rem;
        }
      }
      .form-select {
        cursor: pointer;
        &:focus {
          border-color: $violet;
          outline: 0;
          box-shadow: 0 0 0 0.25rem rgba($color: $violet, $alpha: 0.5);
        }
      }
      hr {
        margin: 1rem 0;
        background-color: $violet;
        border: 0;
        opacity: 1;
      }
    }
  }
  input.cantidad {
    text-align: center;
    border: none;
    background: transparent;
    margin: 0 0.5rem;
    color: var(--primary-color);
    font-size: 1rem;
    width: 35px;
    padding: 0 5px;
    @media (max-width: 500px) {
      margin: 0;
    }
  }
  input.cantidad:focus-visible {
    outline: none;
  }
  input.cantidad::-webkit-outer-spin-button,
  input.cantidad::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  input.cantidad {
    -moz-appearance: textfield;
  }
  .mas,
  .menos {
    border-radius: 50px;
    border: none;
    background-color: #eee;
    color: $violet;
    font-size: 0.8rem;
    padding: 0 5px;
  }
  .form-check-input {
    border: 1px solid $violet;
    &:checked {
      background-color: $violet;
      border-color: $violet;
    }
    &:focus {
      box-shadow: 0 0 0 0.25rem rgba($color: $violet, $alpha: 0.3);
    }
  }
  input {
    cursor: pointer;
  }
  .box-colors {
    position: absolute;
    bottom: -1rem;
    right: 0;
  }
  .select2-container {
    width: 100% !important;
  }
  .select2-container--default .select2-selection--single {
    border: 1px solid transparent;
  }
  .select2-container .select2-selection--single .select2-selection__rendered {
    padding: 0.5rem 0;
    line-height: 1.5;
  }
  .select2-container--default .select2-selection--single .select2-selection__arrow {
    display: none;
  }
  .select2-container--default .select2-results__option--highlighted.select2-results__option--selectable {
    background-color: $violet;
    color: white;
  }
  .select2-container--classic .select2-selection--single:focus {
    background-color: $violet;
    color: white;
  }
  .select2-container .select2-selection--single .select2-selection__rendered {
    width: 100%;
  }
}