.btn-rounded-10 {
  border-radius: 10px;
}
.btn-white-orange {
  color: $text-gray;
  border: 1px solid $orange;
  font-weight: bold;
  background: white;
  transition: all 0.3s;
  &:hover {
    color: white;
    background: $orange;
  }
  &:before {
    background: $orange;
    border-radius: 5px;
  }
  &:focus {
    box-shadow: none;
  }
}
.btn-white-yellow {
  color: $text-gray;
  border: 1px solid $yellow;
  font-weight: bold;
  background: white;
  transition: all 0.3s;
  &:hover {
    color: white;
    background: $yellow;
  }
  &:before {
    background: $yellow;
    border-radius: 5px;
  }
  &:focus {
    box-shadow: none;
  }
}
.btn-violet-white {
  color: white;
  border: 1px solid $violet;
  font-weight: bold;
  background: $violet;
  transition: all 0.3s;
  &:hover {
    color: $violet;
    background: white;
  }
  &:before {
    background: white;
    border-radius: 5px;
  }
  &:focus {
    box-shadow: none;
  }
}
.btn-center-out {
  transform: perspective(1px) translateZ(0);
  overflow: hidden;
}
.btn-center-out:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform: scale(0);
  transition-property: transform;
  transition-duration: 0.3s;
}
.btn-center-out:hover:before,
.btn-center-out:active:before {
  transform: scale(1.15);
}
