footer {
  background-color: $text-gray;
  .border-left {
    border-left: 1px solid $yellow;
  }
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  p, a {
    color: $gray-light;
    margin-bottom: 0;
    line-height: 1.8;
    transition: all 0.3s;
  }
  a:hover {
    color: $yellow;
  }
  .title {
    font-weight: bold;
    color: $yellow;
  }
}